import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import classNames from 'classnames';
import ROUTES from '../../../utils/routes';

import { useDispatch, useSelector } from 'react-redux';
import {
  setMenuAction,
  setScrollingDownAction,
  setScrollingUpAction
} from '../../../actions/templateActions';

import { ReactComponent as MenuIcon } from '../../../images/menu.svg';
import { ReactComponent as VCIcon } from '../../../images/vc.svg';

const Header = () => {
  const dispatch = useDispatch();
  const [scrollTop, setScrollTop] = useState(0);
  const { isColored, isScrollingDown } = useSelector((state) => state.template);
  const newSetMenu = (isActiveMenu) => dispatch(setMenuAction(isActiveMenu));

  useEffect(() => {
    const onScroll = (e) => {
      const scroll = e.target.documentElement.scrollTop;
      dispatch(setScrollingUpAction(scroll < scrollTop));
      dispatch(setScrollingDownAction(scroll > scrollTop));
      setScrollTop(scroll);
    };

    window.addEventListener('scroll', onScroll);

    return () => window.removeEventListener('scroll', onScroll);
  }, [scrollTop, dispatch]);

  return (
    <header
      className={classNames({
        colored: isColored
      })}
    >
      <div
        className={classNames('container', {
          colored: isColored
        })}
      >
        <div className="row">
          <div className="menu__button">
            <button onClick={() => newSetMenu(true)}>
              <MenuIcon />
            </button>
          </div>
        </div>
      </div>
      <div
        className={classNames('container__logo', {
          active: isScrollingDown,
          colored: isColored
        })}
      >
        <div className="divider" />
        <div className="logo">
          <Link to={ROUTES.HOME}>
            <VCIcon />
          </Link>
        </div>
        <div className="divider" />
      </div>
    </header>
  );
};

export default Header;
