import React, { useEffect, useRef } from 'react';
import { useDispatch } from 'react-redux';
import { setColoredAction } from '../../actions/templateActions';

const FullscreenStage = (props) => {
  const stageRef = useRef(null);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(setColoredAction(false));
    const onScroll = (e) => {
      let top = e.target.documentElement.scrollTop;
      dispatch(setColoredAction(top + 100 >= stageRef.current.scrollHeight));
    };
    window.addEventListener('scroll', onScroll);
    return () => window.removeEventListener('scroll', onScroll);
  });

  return (
    <div ref={stageRef} className='row fullscreen-stage'>
      <div className='fullscreen-stage__container'>
        <div className='fullscreen-stage__wrapper'>
          <div className='bg-gradient-gray' />
          {props.children}
        </div>
      </div>
    </div>
  );
};

export default FullscreenStage;
