import {
  SET_MENU,
  SET_HEADER_COLORED,
  SET_SCROLLING_DOWN,
  SET_SCROLLING_UP,
  SET_ACTIVE
} from '../types';

export function setActiveAction(isActive) {
  return (dispatch) => {
    dispatch(setActive(isActive));
  };
}
const setActive = (isActive) => ({
  type: SET_ACTIVE,
  payload: isActive
});

export function setMenuAction(isActiveMenu) {
  return (dispatch) => {
    dispatch(setMenu(isActiveMenu));
  };
}
const setMenu = (isActiveMenu) => ({
  type: SET_MENU,
  payload: isActiveMenu
});

