import {
  SET_ACTIVE,
  SET_MENU,
  SET_SCROLLING_UP,
  SET_SCROLLING_DOWN,
  SET_HEADER_COLORED,
  SET_TITLE
} from '../types';

const initialState = {
  active: false,
  menu: false,
  title: '',
  isScrollingDown: false,
  isScrollingUp: false,
  isColored: false
};

const templateReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_ACTIVE:
      return {
        ...state,
        active: action.payload
      };

    case SET_MENU:
      return {
        ...state,
        menu: action.payload
      };

    case SET_TITLE:
      return {
        ...state,
        title: action.payload
      };

    case SET_SCROLLING_UP:
      return {
        ...state,
        isScrollingUp: action.payload,
        isScrollingDown: !action.payload
      };

    case SET_SCROLLING_DOWN:
      return {
        ...state,
        isScrollingDown: action.payload,
        isScrollingUp: !action.payload
      };

    case SET_HEADER_COLORED:
      return {
        ...state,
        isColored: action.payload
      };

    default:
      return state;
  }
};

export default templateReducer;
