import React from 'react';
import classNames from 'classnames';

import { Link } from 'react-router-dom';
import ROUTES from '../../../utils/routes';

import { ReactComponent as CloseIcon } from '../../../images/close.svg';

import { useDispatch, useSelector } from 'react-redux';
import { setMenuAction } from '../../../actions/templateActions';

const Aside = () => {
  const dispatch = useDispatch();
  const { menu } = useSelector((state) => state.template);
  const nSetMenu = (isActiveMenu) => dispatch(setMenuAction(isActiveMenu));

  const asideClass = classNames({
    active: menu
  });

  return (
    <aside className={asideClass}>
      <div className="content">
        <div className="content__wrapper">
          <div className="content__overlay">
            <div className="container">
              <div className="content__height">
                <div className="row justify-content-center">
                  <div className="col-22 pt-4">
                    <div className="content__close">
                      <div className="content__close__wrapper">
                        <button onClick={() => nSetMenu(false)}>
                          <span>
                            <CloseIcon />
                          </span>
                          <span>Cerrar Menú</span>
                        </button>
                      </div>
                    </div>
                  </div>
                  <div className="col-20 offset-2">
                    <ul className="menu">
                      <li>
                        <Link to={ROUTES.HOME} onClick={() => nSetMenu(false)}>
                          Home
                        </Link>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </aside>
  );
};

export default Aside;
